.poll-option {
    min-height: 36px;
    width: 100%;
    display: flex;
    align-items: center;
    background: transparent;
    position: relative;
    box-sizing: border-box;
    margin: 6px 0;
}

.poll-option-wrapper {
    width: 100%;
}

.poll-option-text-wrapper {
    display: flex;
    flex-direction: row;
    align-items: baseline;
}

.poll-option-text {
    margin-left: 41px;
}

.poll-option-bottom-border {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    border-bottom: 1px solid var(--border);
    margin-left: 41px;
}

.poll-option-progress-root {
    background: transparent;
    margin: 2px 0 0 41px;
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    border-radius: 2px;
}

.poll-option-progress-bar {
    transition: transform 0.2s linear;
    border-radius: 2px;
}
